.page-main__wrapper {
  min-height: 100%;
}

.page-main__description {
  font-size: 13px;
  line-height: 24px;
  color: @text-darkgrey;
  margin-left: 30px;
  margin-top: 16px;

  .text-none--write {
    display: none;
  }

  @media (min-width: @tablet-width) {
    font-size: 17px;
    margin-top: 68px;
    line-height: 30px;
    margin-left: 50px;

    .text-none {
      display: block;
    }


    .page-main__description--none {
      display: none;
    }
  }

  @media (min-width: @desktop-width) {
    padding-left: 175px;
    margin-top: 120px;

    .text-none {
      display: none;
    }

    .text-none--write {
      display: block;
    }
  }
}

.radio-feed {
  margin-left: 8px;

  @media (min-width: @tablet-width) {
    margin-top: 58px;
    margin-left: 27px;
  }

  @media (min-width: @desktop-width) {
    margin-left: 88px;
    margin-top: 57px;
  }
}

.radio-feed__legend {
  position: relative;
  padding-top: 6px;
  margin-bottom: 27px;

  &::before {
    content: "";
    position: absolute;
    width: 200px;
    height: 1px;
    top: 17px;
    left: 60px;
    background-image: url("../img/borderlayer.svg");
  }

  @media (min-width: @tablet-width) {
    &::before {
      display: none;
    }
  }
}

.radio-feed__input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}

.radio-feed__input + .radio-feed__label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 18px;
  color: @basik-black;
  width: 220px;
  margin-left: 10px;
  margin-bottom: 13px;

  @media (min-width: @tablet-width) {
    margin-left: 14px;
    margin-bottom: 27px;
    width: 350px;
    font-size: 19px;
  }

  @media (min-width: @desktop-width) {
    margin-left: -41px;
  }

  &::before {
    content: "";
    display: inline-block;
    vertical-align: baseline;
    width: 36px;
    height: 36px;
    flex-shrink: 0;
    flex-grow: 0;
    border: 2px solid @text-darkgrey;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center center;
    margin-right: 18px;

    @media (min-width: @tablet-width) {
      width: 45px;
      height: 45px;
      margin-right: 28px;
    }
  }
}

.checkbox-feed__input + .checkbox-feed__label:hover::before,
.radio-feed__input + .radio-feed__label:hover::before {
  border: 2px solid @basik-border;
}

.checkbox-feed__input:focus + .checkbox-feed__label::before,
.radio-feed__input:focus + .radio-feed__label::before {
  border: 2px solid rgba(0, 0, 0, 0.3);
}

.radio-feed__input:checked + .radio-feed__label::before {
  width: 36px;
  height: 36px;
  background-image: url("../img/checked.svg");

  @media (min-width: @tablet-width) {
    width: 46px;
    height: 45px;
  }
}

.checkbox-feed__input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}

.checkbox-feed__legend {
  position: relative;
  margin-bottom: 18px;
  padding-left: 4px;
  padding-right: 108px;

  &::before {
    content: "";
    position: absolute;
    width: 200px;
    height: 1px;
    top: 9px;
    left: 66px;
    background-image: url("../img/borderlayer.svg");

    @media (min-width: @tablet-width) {
      display: none;
    }
  }

  @media (min-width: @tablet-width) {
    padding-left: 25px;
    padding-top: 13px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 86px;
    padding-top: 15px;
  }
}

.checkbox-feed__wrapper {
  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: min-content 1fr;
  }

  @media (min-width: @desktop-width) {
    height: 170px;
  }
}

.checkbox-feed {
  @media (min-width: @tablet-width) {
    background-color: @background-grey;
    margin: 0;
    height: 244px;
    padding-top: 50px;
  }

  @media (min-width: @desktop-width) {
    background-color: @background-grey;
    margin: 0;
    padding-top: 48px;
    height: 170px;
  }
}

.checkbox-feed__input + .checkbox-feed__label {
  display: inline-flex;
  align-items: center;
  user-select: none;
  font-style: normal;
  font-weight: bold;
  font-size: 17px;
  line-height: 18px;
  color: @basik-black;
  width: 220px;
  margin-left: 16px;
  margin-bottom: 23px;

  @media (min-width: @tablet-width) {
    margin-bottom: 24px;
    font-size: 19px;
    line-height: 20px;
  }

  @media (min-width: @desktop-width) {
    margin-left: -38px;
  }

  &::before {
    content: "";
    display: inline-block;
    vertical-align: baseline;
    width: 34px;
    height: 34px;
    border: 2px solid @text-darkgrey;
    background-repeat: no-repeat;
    background-position: center center;
    margin-right: 22px;

    @media (min-width: @tablet-width) {
      width: 48px;
      height: 48px;
      margin-right: 30px;
    }
  }
}

.checkbox-feed__input:checked + .checkbox-feed__label::before {
  background-image: url("../img/checbox.svg");

  @media (min-width: @tablet-width) {
    background-image: url("../img/checkboxtablet.svg");
  }
}

.checkbox-feed__list {
  display: flex;
  flex-direction: column;
  font-size: 19px;
  font-weight: 700;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: min-content min-content;
    grid-template-rows: repeat(3, 1fr);
    column-gap: 26px;
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, min-content);
    column-gap: 81px;
    margin-left: 100px;
  }
}

.info-feed {
  padding: 0;
  margin-left: 20px;
  margin-top: -5px;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: min-content 1fr;
    grid-template-rows: 1fr;
    gap: 115px;
    padding-top: 64px;
    height: 184px;
  }

  @media (min-width: @desktop-width) {
    padding-left: 80px;
    padding-top: 59px;
    margin: 0 auto;
    height: 183px;
  }
}

.info-feed__legend {
  position: relative;
  padding-bottom: 2px;

  &::before {
    content: "";
    position: absolute;
    width: 200px;
    height: 1px;
    top: 13px;
    left: 60px;
    background-image: url("../img/borderlayer.svg");
  }

  @media (min-width: @tablet-width) {
    padding-top: 4px;
    padding-left: 20px;

    &::before {
      display: none;
    }
  }

  @media (min-width: @desktop-width) {
    margin-left: -40px;
  }
}

.info-feed__item {
  padding-left: 10px;
  padding-top: 5px;
  padding-bottom: 15px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-direction: row;
    padding-bottom: 33px;
  }

  @media (min-width: @desktop-width) {
    margin-left: -52px;
  }
}

.info-feed__label {
  @media (min-width: @tablet-width) {
    font-size: 19px;
  }
}

.info-feed__input {
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 2px solid @basik-black;
  width: 256px;
  height: 44px;
  font-size: 19px;
  line-height: 18px;
  color: @text-darkgrey;

  @media (min-width: @tablet-width) {
    margin-top: -10px;
    margin-left: 25px;
    width: 404px;
    padding-left: 20px;
  }

  @media (min-width: @desktop-width) {
    width: 608px;
  }

  &::placeholder {
    color: @basik-black;
    opacity: 0.3;
    font-size: 17px;
    line-height: 18px;
    letter-spacing: 0.3px;

    @media (min-width: @tablet-width) {

      letter-spacing: 0.3px;
      font-size: 19px;
    }
  }

  &:hover {
    border-bottom: 2px solid @basik-border;
  }

  &:focus {
    border-bottom: 2px solid @basik-border;
    outline: none;
  }
}

.info-feed__input--patronomic,
.info-feed--famely {
  @media (min-width: @tablet-width) {
    width: 352px;
    margin-left: 27px;
  }

  @media (min-width: @desktop-width) {
    width: 558px;
  }
}

.tell-feed_wrapper {
