.page-form__button {
  width: 260px;
  height: 52px;
  border: 2px solid @text-darkgrey;
  text-transform: uppercase;
  font-weight: 700;
  margin-left: 28px;
  margin-top: 13px;
  font-size: 17px;
  line-height: 16px;

  @media (min-width: @tablet-width) {
    width: 230px;
    margin-left: 48px;
    margin-top: 17px;
  }

  @media (min-width: @desktop-width) {
    margin-left: 130px;
    margin-top: 12px;
  }
}

.page-form__text {
  font-size: 14px;
  padding-left: 28px;
