    margin-top: 25px;
  }
}

@media (min-width: @tablet-width) {
  .radio-feed {
    display: grid;
    grid-template-columns: min-content 1fr;
    grid-template-rows: 1fr;
    gap: 120px;
    height: 166px;

    @media (min-width: @desktop-width) {
      height: 164px;
    }
  }

  .radio-feed__list {
    grid-column-start: 2;
    grid-column-end: 2;
    font-size: 19px;
    font-weight: 700;
  }

  .radio-feed__legend {
    grid-row-start: 1;
    grid-row-end: 1;
    padding-top: 12px;
  }
}

.page-form {
  border: none;
}

.legend-text {
  font-weight: 700;
  text-transform: uppercase;
  color: @background-lightgreen;
  margin-left: 10px;
  margin-top: 0;
}

.tell-feed__label--contacts {
  display: none;
}

.email-feed__label--contacts {
  display: none;
}

.info-feed__label--personal {
  font-weight: 700;
}

.info-feed__label--contacts {
  font-weight: 700;
  text-transform: uppercase;
  color: @background-lightgreen;
}

.add-feed__label--contacts {
